.header{
    width: 100%; 
    display: flex;
    align-items: center;
    margin-bottom: 12px;
}

.header-logo{
    min-width: 140px;
    height: 27px;
    margin-right: 24px;
}


.header-right{
    display: flex;
    flex: 1;
    justify-content: space-between;
    align-items: center;
    height: 72px;
}

.header-location-search-container{
    height: 54px;
    display: flex;
    align-items: center;
    border-radius: 8px;
    box-shadow: var(--primary-box-shadow);
    border: 1px solid var(--border-white);
    width: 70%;
    background-color: var(--primary-bg);
}

.location-wrapper{
    display: flex;
    flex: 1;
    justify-content: space-between;
    padding: 0px 10px;
}

.location-icon-name{
    display: flex;
    color: var(--zomato-gray);
}

.location-icon{
    color: var(--zomato-red);
    font-size: 20px;
    margin-right: 5px;
}

.location-search-separator{
    height: 20px;
    border: .5px solid var(--gray);
}

.header-searchBar{
    flex: 2;
    margin-right: 8px;
}

.search-icon{
    font-size: 20px;
    color: var(--zomato-gray);
    margin: 0 12px;
}

.search-input{
    font-size: 14px;
    border: none;
    outline: none;
    width: 60%;
    color: var(--zomato-gray);
}

input::placeholder{
    color: var(--zomato-gray);
}

.profile-wrapper{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    width: 15%;
}

.header-profile-image{
   height: 40px;
   border-radius: 50%; 
}

.header-username{
    font-size: 16px;
    font-weight: 800;
}

.profile-options-icon{
    font-size: 20px;
}