.collection-wrapper {
  background-color: var(--inactive-color);
}

.collection {
  padding: 15px 0px 40px;
}

.collection-subtitle-row {
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.collection-subtitle-text {
  color: var(--subtitle-text);
  font-size: 18px;
}

.collection-location {
  color: var(--zomato-red);
  display: flex;
}

.collection-cover {
  height: 320px;
  width: 260px;
  margin-right: 20px;
  border-radius: 6px;
  position: relative;
}

.collection-image {
  height: 100%;
  width: 100%;
  object-fit: cover;
  border-radius: 6px;
}

.gradient-bg {
  position: absolute;
  height: 100%;
  width: 100%;
  background: var(--bg-gradient);
  left: 0;
  top: 0;
  border-radius: 6px;
}

.collection-card-title{
    position: absolute;
    left: 16px;
    bottom: 30px;
    font-size: 18px;
    color: var(--primary-bg);
}

.collection-card-subtitle{
    display: flex;
    color: var(--primary-bg);
    position: absolute;
    left: 16px;
    bottom: 10px;
    font-size: 14px;
}